<template>
	
	<div>
		
	<el-row :gutter="20">
		<el-col :span="12" :offset="6">
			<div>
				<el-form ref="form" :model="form" label-width="80px">
				  <el-form-item label="用户名">
				    <el-input v-model="form.username"></el-input>
				  </el-form-item>
				  <el-form-item label="手机">
				    <el-input v-model="form.mobile"></el-input>
				  </el-form-item>
				  <el-form-item label="密码">
				    <el-input v-model="form.password"></el-input>
				  </el-form-item>
				  <el-form-item>
				    <el-button type="primary" @click="addUser">立即创建</el-button>
				    <el-button>取消</el-button>
				  </el-form-item>
				</el-form>
				
			</div>
		</el-col>
	</el-row>
	
	</div>	
	<!-- <div class="adduser">
		<el-input v-model="username" placeholder='用户名'></el-input>
		<el-input v-model="mobile" placeholder='手机'></el-input>
		<el-input v-model="password" placeholder='密码'></el-input>
		<el-button   @click="addUser()">注册</el-button>
	</div> -->
</template>

<script>
	import {saveUser} from '../../api/index.js'
	import {dateUtil} from '../../util/dateUtil.js'
	import user from '../../entity/sysUser.js'
	
export default {
  name: 'HelloWorld',
  data(){
	  return{
		  form:{  
			"mobile": "",
			"username": "",
			"password": "",
			}
	  }
  },
  methods:{
	addUser(){
		console.log("时间:"+dateUtil())
		user.name = this.form.username
		user.username = this.form.username
		user.password = this.form.password
		user.mobile = this.form.mobile
		user.createTime = dateUtil();
		console.log("用户："+JSON.stringify(user))
		saveUser(user).then(r =>{
			console.log("注册用户："+JSON.stringify(r))
		})
	}
  },
  props: {
    msg: String
  },
}
</script>

